<template>
  <div class="root">
    <div class="container">
      <div class="layout">
        <div class="content">{{Question}}</div>
        <div class="content">{{Answer}}</div>
        <br>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DisplayQA",
  props:{
    Question: String,
    Answer:   String,
  }
}
</script>

<style scoped>
.root{
  /*border: red solid 1px;*/
}

.container{
  /*border: blue solid 1px;*/
}

@media screen and (min-width: 800px){
  .layout{
    /*border: yellow solid 1px;*/
    width: 56%;
    display: block;
    margin: 0 auto;
    padding-top: 2vh;
  }
}
@media screen and (max-width: 800px){
  .layout{
    /*border: yellow solid 1px;*/
    width: 80%;
    display: block;
    margin: 0 auto;
  }

}
.content{
  display: block;
  /*border: #1E90FF solid 1px;*/
  margin-bottom: 1.5vh;
  font-family: "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: grey;

  font-size: 2vh;
}
</style>



